CAAqC;EAC5D;;EAEA;IACE,mBAAmB,EAAE,2CAA2C;EAClE;;EAEA;MACI,kBAAkB;MAClB,iBAAiB;EACrB;;EAEA,4CAA4C;EAC5C;MACI,eAAe;EACnB;;EAEA;;MAEI,eAAe;EACnB;;EAEA;MACI,eAAe;EACnB;AACF;;AAEA,yDAAyD;AACzD;EACE;IACE,6BAA6B;IAC7B,iCAAiC;IACjC,uCAAuC;IACvC,gBAAgB;EAClB;AACF;;AAEA,sCAAsC;AACtC;EACE,aAAa;EACb,uBAAuB,EAAE,wBAAwB;EACjD,mBAAmB,EAAE,sBAAsB;EAC3C,aAAa,EAAE,oBAAoB;EACnC,cAAc,EAAE,gBAAgB;EAChC,eAAe,EAAE,iBAAiB;EAClC,YAAY,EAAE,qBAAqB;EACnC,iBAAiB,EAAE,qBAAqB;EACxC,mBAAmB,EAAE,oBAAoB;EACzC,yCAAyC,EAAE,qBAAqB;AAClE;;AAEA;EACE,aAAa,EAAE,kCAAkC;AACnD","sourcesContent":["/* styles.css - Complete CSS with Mobile Modal Fix */\n\n/* Body and Container Styles */\nbody { \n  font-family: 'Inter', Arial, sans-serif; \n  background: #f9fafb; \n  color: #222; \n  margin: 0; \n}\n.container { \n  max-width: 600px; \n  margin: 0 auto; \n  padding: 32px 16px; \n}\n\n.button-container { \n  display: flex; /* Use flexbox layout */\n  justify-content: center; /* Center the buttons horizontally */\n  gap: 1rem; /* Space between buttons */\n  margin-top: 1.5rem; /* Space above the button container */\n}\n\n/* Header Styles */\nheader { \n  text-align: center; \n  margin-bottom: 2rem; \n}\nh1 { \n  font-size: 2.2rem; \n  font-weight: 700; \n  margin: 0 0 0.5rem 0; \n}\n\n/* Game Info and Stats Styles */\n.game-info { \n  display: flex; \n  justify-content: center; \n  align-items: center; \n  margin-bottom: 2rem; \n  gap: 2rem; \n}\n.game-stats { \n  display: flex; \n  gap: 2rem; \n  justify-content: center; \n  align-items: center; \n  width: 100%; \n}\n.stat { \n  display: flex; \n  flex-direction: column; \n  align-items: center; \n  background: #fff; \n  border-radius: 10px; \n  box-shadow: 0 1px 4px rgba(0,0,0,0.04); \n  padding: 0.7rem 1.2rem; \n  min-width: 80px; \n  border: 1px solid #e5e7eb; \n}\n.stat-value { \n  font-size: 1.5rem; \n  font-weight: 600; \n  margin-bottom: 0.2rem; \n}\n.stat-label { \n  font-size: 0.85rem; \n  color: #6b7280; \n  text-transform: uppercase; \n  letter-spacing: 1px; \n}\n\n/* Timer Styles */\n#timer.danger { color: #ef4444; }\n#timer.warning { color: #f59e42; }\n\n/* Progress Bar Styles */\n.progress-container { \n  width: 100%; \n  background: #e5e7eb; \n  border-radius: 4px; \n  margin: 1.5rem 0 2rem 0; \n  height: 8px; \n  overflow: hidden; \n}\n.progress-bar { \n  height: 100%; \n  background: #2563eb; \n  border-radius: 4px; \n  transition: width 0.3s; \n}\n\n/* Question Styles */\n.question { \n  font-size: 1.3rem; \n  font-weight: 500; \n  text-align: center; \n  margin-bottom: 2rem; \n}\n\n/* Flag Options Styles */\n.flag-options { \n  display: flex; \n  flex-direction: column; \n  gap: 1rem; \n}\n.flag-option { \n  display: flex; \n  flex-direction: column; \n  align-items: center; \n  cursor: pointer; \n  transition: transform 0.15s; \n  position: relative; \n  background: #fff; \n  border-radius: 10px; \n  box-shadow: 0 1px 6px rgba(0,0,0,0.06); \n  padding: 0.7rem 0.7rem 0.3rem 0.7rem; \n  border: 1.5px solid transparent; \n}\n.flag-option:hover { \n  transform: translateY(-4px) scale(1.04); \n  box-shadow: 0 4px 16px rgba(37,99,235,0.08); \n}\n.flag-img { \n  width: 90px; \n  height: 60px; \n  object-fit: cover; \n  border-radius: 6px; \n  border: 2.5px solid transparent; \n  background: #eee; \n  transition: border-color 0.2s, box-shadow 0.2s; \n}\n.flag-img.correct { \n  border-color: #22c55e; \n  box-shadow: 0 0 0 4px #22c55e33; \n  animation: pulse-success 0.7s; \n}\n.flag-img.incorrect { \n  border-color: #ef4444; \n  animation: shake 0.5s; \n}\n\n/* Feedback Styles */\n.feedback { \n  margin: 1.5rem 0 0.5rem 0; \n  text-align: center; \n  font-size: 1.2rem; \n  font-weight: 600; \n  min-height: 2rem; \n  color: #1e40af; \n}\n\n/* Button Styles */\n.button { \n  background: #2563eb; \n  color: #fff; \n  border: none; \n  padding: 0.8rem 1.5rem; \n  font-size: 1rem; \n  border-radius: 6px; \n  cursor: pointer; \n  transition: background 0.18s; \n  font-weight: 600; \n  box-shadow: 0 1px 4px rgba(0,0,0,0.04); \nmargin-bottom: 0.5rem; /* Margins for the button */\nmargin-top: 1.5rem;\nmargin-right: 0.5rem;\nmargin-left: 0.5rem;\n}\n.button:hover { \n  background: #1e40af; \n}\n.button.success { \n  background: #22c55e; \n}\n.button.success:hover { \n  background: #16a34a; \n}\n\n/* Difficulty Buttons Styles */\n.difficulty-btn {\n  padding: 0.5rem 1.2rem; /* Padding for the button */\nmargin-bottom: 1.5rem; /* Margins for the button */\nmargin-right: 0.5rem;\nmargin-left: 0.5rem;\n  background: #f3f4f6; /* Background color */\n  border: none; /* No border */\n  border-radius: 4px; /* Rounded corners */\n  cursor: pointer; /* Pointer cursor on hover */\n  font-weight: 500; /* Font weight */\n  font-size: 1rem; /* Font size */\n  color: #222; /* Text color */\n  transition: background 0.15s, color 0.15s; /* Transition effects */\n}\n\n.difficulty-btn.active {\n  background: #2563eb; /* Active background color */\n  color: #fff; /* Active text color */\n}\n\n/* Modal Overlay - Updated */\n.modal { \n  position: fixed; \n  top: 0; \n  left: 0; \n  width: 100vw; \n  height: 100vh; \n  /* Use dynamic viewport height for better mobile support */\n  height: 100dvh; \n  background: rgba(30, 41, 59, 0.32);\n  display: flex; \n  align-items: center; \n  justify-content: center; \n  z-index: 1000; \n  opacity: 0; \n  visibility: hidden; \n  transition: opacity 0.3s, visibility 0.3s; \n  /* Ensure scrolling if needed */\n  overflow-y: auto;\n  padding: 10px;\n  box-sizing: border-box;\n}\n\n.modal.active { \n  opacity: 1; \n  visibility: visible; \n}\n\n/* Modal Content - Updated */\n.modal-content {\n  padding: 1.5rem 1rem 1rem 1rem;\n  width: 100%;\n  max-width: 600px; /* Set a reasonable max-width */\n  /* Use dynamic viewport height with fallback */\n  max-height: min(90vh, 90dvh);\n  overflow-y: auto;\n  display: flex;\n  flex-direction: column;\n  background: #f9fafb;\n  border-radius: 18px;\n  box-shadow: 0 8px 32px rgba(30, 41, 59, 0.18), 0 1.5px 6px rgba(0,0,0,0.10);\n  border: 1px solid #e5e7eb;\n  animation: modal-fade-in 0.4s cubic-bezier(.4,0,.2,1);\n  font-family: 'Inter', Arial, sans-serif;\n  /* Remove margin that was causing issues */\n  margin: 0;\n  /* Ensure minimum space for content */\n  min-height: min(50vh, 400px);\n}\n\n/* Ensure all elements use border-box sizing */\n.modal-content * {\nbox-sizing: border-box;\n}\n\n@keyframes modal-fade-in {\n  from { opacity: 0; transform: translateY(32px) scale(0.98);}\n  to { opacity: 1; transform: translateY(0) scale(1);}\n}\n\n/* Modal Header */\n.modal-header { \n  display: flex; \n  justify-content: space-between; \n  align-items: center; \n  margin-bottom: 1.2rem;\n  border-bottom: 1px solid #e5e7eb;\n  padding-bottom: 0.7rem;\n}\n.modal-header h2 {\n  font-size: 1.35rem;\n  font-weight: 700;\n  letter-spacing: 0.02em;\n  color: #1e293b;\n  margin: 0;\n}\n\n/* Close Button */\n.modal-close { \n  background: none; \n  border: none; \n  font-size: 1.7rem; \n  cursor: pointer; \n  color: #64748b;\n  transition: color 0.18s;\n  border-radius: 50%;\n  width: 2.2rem;\n  height: 2.2rem;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.modal-close:hover {\n  background: #e0e7ef;\n  color: #1e293b;\n}\n\n/* Styles for the new High Scores Modal UI */\n.high-scores-difficulty-group {\nmargin-bottom: 1.5rem;\npadding-bottom: 1rem;\nborder-bottom: 1px solid #e0e7ef;\nwidth: 100%; /* Ensure full width */\nbox-sizing: border-box; /* Include padding/border in width calculation */\n}\n\n.high-scores-difficulty-group:last-of-type {\nborder-bottom: none; /* No border for the last group */\nmargin-bottom: 0;\n}\n\n.difficulty-group-header {\nfont-size: 1.25rem; /* Larger font for difficulty headers */\nfont-weight: 600;\ncolor: #374151; /* Darker gray for header text */\nmargin-bottom: 0.75rem;\n}\n\n.high-scores-header {\ndisplay: flex;\njustify-content: space-between;\npadding: 0.5rem 0;\nmargin: 0 0 0.5rem 0; /* Remove horizontal margins */\nborder-bottom: 2px solid #e5e7eb;\nfont-weight: 600;\ncolor: #4b5563;\nfont-size: 0.9rem;\nwidth: 100%;\nbox-sizing: border-box;\n}\n\n/* Adjust column widths to be more flexible */\n.header-rank, .entry-position {\nflex: 0 0 15%; /* Use percentage instead of fixed px */\ntext-align: center;\nmin-width: 30px; /* Minimum width for very small screens */\n}\n\n.header-name, .entry-name {\nflex: 1; /* Take remaining space */\ntext-align: left;\nmin-width: 80px; /* Minimum width */\npadding-right: 8px; /* Small padding instead of margin */\noverflow-wrap: break-word; /* Allow long names to wrap */\n}\n\n.header-score, .entry-points {\nflex: 0 0 18%; /* Use percentage */\ntext-align: center;\nmin-width: 50px;\n}\n\n.header-accuracy, .entry-accuracy {\nflex: 0 0 20%; /* Use percentage */\ntext-align: center;\nmin-width: 60px;\n}\n\n.header-date, .entry-date {\nflex: 0 0 22%; /* Use percentage */\ntext-align: center;\nmin-width: 70px;\n}\n\n.high-scores-list {\nlist-style: none;\npadding: 0;\nmargin: 0;\nwidth: 100%;\nbox-sizing: border-box;\n}\n\n.high-score-entry {\ndisplay: flex;\nalign-items: center;\npadding: 0.6rem 0;\nmargin: 0; /* Remove any margins */\nfont-size: 0.95rem;\ncolor: #4b5563;\nborder-bottom: 1px dashed #f3f4f6;\nwidth: 100%;\nbox-sizing: border-box;\n}\n\n/* Remove individual margins and use padding for spacing */\n.header-rank, .header-name, .header-score, .header-accuracy,\n.entry-position, .entry-name, .entry-points, .entry-accuracy {\nmargin-right: 0; /* Remove margins */\npadding-right: 4px; /* Use small padding instead */\n}\n\n.header-date, .entry-date {\nmargin-right: 0;\npadding-right: 0;\n}\n\n.modal-empty {\nmargin-top: 0.5rem;\nfont-size: 0.8rem;\nfont-style: italic;\ncolor: #555;\n}\n\n/* Modal Footer - Updated to be sticky */\n.modal-footer {\n  margin-top: auto;\n  padding: 1rem 0 0.5rem 0;\n  border-top: 1px solid #e0e7ef;\n  display: flex;\n  justify-content: flex-end;\n  flex-shrink: 0; /* Prevent shrinking */\n  /* Make footer sticky to bottom */\n  position: sticky;\n  bottom: 0;\n  background: #f9fafb; /* Match modal background */\n  /* Add slight shadow to show separation */\n  box-shadow: 0 -2px 8px rgba(0,0,0,0.05);\n}\n\n.modal-empty {\ncolor: #666;\ntext-align: center;\npadding: 1rem;\n}\n\n/* Game Over Flag Display Styles */\n.game-over-flags-container {\ndisplay: flex;\njustify-content: center;\ngap: 2rem;\nmargin-top: 2rem;\n}\n\n.flag-column {\ntext-align: center;\n}\n\n.flag-column h3 {\nmargin-bottom: 1rem;\n}\n\n.flag-list {\ndisplay: flex;\nflex-direction: column;\ngap: 1rem;\n}\n\n.flag-item {\ndisplay: flex;\nflex-direction: column;\nalign-items: center;\n}\n\n.flag-item img {\nwidth: 120px;\nheight: 80px;\nobject-fit: contain;\nborder-radius: 6px;\n}\n\n.flag-item span {\nmargin-top: 0.5rem;\nfont-size: 0.9rem;\n}\n\n.empty-message {\ncolor: #6b7280;\n}\n\n.difficulty-selector {\ndisplay: flex;\njustify-content: center;\ngap: 1rem;\nmargin-bottom: 2rem;\n}\n\n/* Centered Text Style */\n.centered-text {\ntext-align: center;\nmargin-bottom: 2rem;\n}\n\n/* Add sovereign-state filter toggle styles */\n.toggle-container {\ndisplay: flex;\nalign-items: center;\njustify-content: center;\ngap: 0.5rem;\nmargin: 1rem 0;\n}\n.toggle-switch {\nposition: relative;\nwidth: 40px;\nheight: 20px;\ndisplay: inline-block;\n}\n.toggle-switch input {\nopacity: 0;\nwidth: 0;\nheight: 0;\n}\n.switch-slider {\nposition: absolute;\ncursor: pointer;\ntop: 0;\nleft: 0;\nright: 0;\nbottom: 0;\nbackground-color: #e5e7eb;\nborder-radius: 20px;\ntransition: background-color 0.3s;\n}\n.switch-slider::before {\nposition: absolute;\ncontent: \"\";\nheight: 16px;\nwidth: 16px;\nleft: 2px;\nbottom: 2px;\nbackground-color: #fff;\nborder-radius: 50%;\ntransition: transform 0.3s;\n}\n.toggle-switch input:checked + .switch-slider {\nbackground-color: #2563eb;\n}\n.toggle-switch input:checked + .switch-slider::before {\ntransform: translateX(20px);\n}\n.toggle-label {\nfont-size: 1rem;\ncolor: #222;\n}\n\n/* New styles for high-score-note (the note to explain what the asterisk means)*/\n.high-score-note {\n  margin-top: 0.5rem;\n  font-size: 0.8rem;\n  font-style: italic;\n  color: #555;\n}\n\n/* Rotates emoji 45 degrees. Using this to turn a black square into a black diamond. */\n.rotate-emoji {\ndisplay: inline-block;\ntransform: rotate(45deg);\nfont-size: 1.25rem;\n}\n\n.new-high-score-banner {\nbackground: linear-gradient(45deg, #ffd700, #ffed4e);\ncolor: #1a1a1a;\ntext-align: center;\npadding: 15px;\nmargin: 20px 0;\nborder-radius: 10px;\nfont-size: 1.5rem;\nfont-weight: bold;\nbox-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);\nposition: relative;\nanimation: celebrateGlowBounce 2s ease-in-out infinite;\n}\n.new-high-score-banner::after {\ncontent: \"\";\nfont-size: 3rem;\nposition: absolute;\ntop: -10px;\nright: 10px;\nanimation: sparkle 1s ease-in-out infinite alternate;\n}\n.new-high-score-banner span {\ndisplay: inline-block;\nanimation: textPop 0.5s ease-in-out infinite alternate;\n}\n@keyframes celebrateGlowBounce {\n0%, 100% {\n  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);\n  transform: scale(1) translateY(0);\n}\n50% {\n  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.6);\n  transform: scale(1.02) translateY(-8px);\n}\n}\n@keyframes sparkle {\n0% {\n  opacity: 0.3;\n  transform: scale(0.8);\n}\n100% {\n  opacity: 1;\n  transform: scale(1.2);\n}\n}\n@keyframes textPop {\n0% {\n  transform: scale(1);\n}\n100% {\n  transform: scale(1.1);\n}\n}\n\n/*Leaderboard Banner Styles - shows when you make the leaderboard but not the highest score ever*/\n.leaderboard-banner {\nbackground: linear-gradient(45deg, #f3f4f6, #e5e7eb);\ncolor: #374151;\ntext-align: center;\npadding: 15px;\nmargin: 20px 0;\nborder-radius: 10px;\nfont-size: 1.3rem;\nfont-weight: bold;\nbox-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);\nposition: relative;\nanimation: leaderboardGlow 2s ease-in-out infinite;\nborder: 2px solid #d1d5db;\n}\n\n.leaderboard-banner::after {\ncontent: \"\";\nposition: absolute;\ntop: -10px;\nright: 10px;\nanimation: trophyBounce 1.5s ease-in-out infinite alternate;\n}\n\n.leaderboard-banner span {\ndisplay: inline-block;\nanimation: subtleTextPop 0.8s ease-in-out infinite alternate;\n}\n\n@keyframes leaderboardGlow {\n0%, 100% {\n  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);\n  transform: scale(1) translateY(0);\n}\n50% {\n  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);\n  transform: scale(1.01) translateY(-4px);\n}\n}\n\n@keyframes trophyBounce {\n0% {\n  opacity: 0.6;\n  transform: scale(0.9) rotate(-5deg);\n}\n100% {\n  opacity: 1;\n  transform: scale(1.1) rotate(5deg);\n}\n}\n\n@keyframes subtleTextPop {\n0% {\n  transform: scale(1);\n}\n100% {\n  transform: scale(1.05);\n}\n}\n\n/*Styling for the tooltop ? icon and text shown next to the difficulty buttons*/\n.tooltip-icon {\ndisplay: inline-flex;\njustify-content: center;\nalign-items: center;\nbackground: #ddd;\ncolor: #333;\nborder-radius: 50%;\nwidth: 16px;\nheight: 16px;\nfont-size: 12px;\nline-height: 16px;\nmargin-left: 8px;\ncursor: pointer;\nposition: relative;\nvertical-align: super;\n}\n\n.tooltip-text {\nposition: absolute;\nleft: 100%;\ntop: 50%;\ntransform: translateY(-50%);\nbackground: #333;\ncolor: #fff;\npadding: 8px;\nborder-radius: 4px;\nfont-size: 14px;\nmin-width: 200px;\nz-index: 10;\nmargin-left: 8px;\nwhite-space: nowrap;\n}\n\n.player-name-form {\nmargin-top: 20px;\nmargin-bottom: 20px;\ndisplay: flex;\njustify-content: center;\n}\n\n.player-name-input {\npadding: 0.8rem 1.5rem;\nborder: 1px solid #ccc;\nborder-radius: 6px;\nfont-size: 1.5rem;\nwidth: 200px;\nmargin-right: 0.5rem;\n}\n\n.submit-button {\nbackground: #2563eb; \ncolor: #fff; \nborder: none; \npadding: 0.8rem 1.5rem; \nfont-size: 1rem; \nborder-radius: 6px; \ncursor: pointer; \ntransition: background 0.18s; \nfont-weight: 600; \nbox-shadow: 0 1px 4px rgba(0,0,0,0.04); \nmargin-bottom: 0.5rem;\nmargin-top: 1.5rem;\nmargin-right: 0.5rem;\nmargin-left: 0.5rem;\n}\n\n.submit-button:hover {\nbackground-color:#1e40af;\n}\n\n/* Updated Media Queries */\n@media (max-width: 700px) { \n  .container { \n      padding: 16px 2vw; \n  } \n  .flag-options { \n      gap: 1rem; \n  } \n  .stat { \n      min-width: 60px; \n      padding: 0.5rem 0.7rem; \n  } \n  .flag-img { \n      width: 60px; \n      height: 40px; \n  }\n  \n  /* Mobile modal adjustments */\n  .modal {\n    padding: 5px;\n    /* Use dynamic viewport height with fallback */\n    height: 100dvh;\n    height: 100vh; /* Fallback for browsers without dvh support */\n  }\n  \n  .modal-content {\n    padding: 1rem;\n    /* Use more of the available height on mobile */\n    max-height: min(95vh, 95dvh);\n    /* Ensure some margin from screen edges */\n    margin: 0;\n  }\n  \n  .modal-footer {\n    padding: 0.75rem 0 0.75rem 0; /* Increased padding for better touch targets */\n  }\n  \n  .high-scores-header, .high-score-entry {\n      font-size: 0.8rem;\n      padding: 0.4rem 0;\n  }\n  \n  /* Adjust column widths for mobile */\n  .header-rank, .entry-position {\n      flex: 0 0 12%;\n      min-width: 25px;\n  }\n  \n  .header-score, .entry-points {\n      flex: 0 0 16%;  \n      min-width: 40px;\n  }\n  \n  .header-accuracy, .entry-accuracy {\n      flex: 0 0 18%;\n      min-width: 45px;\n  }\n  \n  .header-date, .entry-date {\n      flex: 0 0 20%;\n      min-width: 55px;\n  }\n  \n  /* Optional: For better mobile touch targets */\n  .tooltip-icon {\n      width: 24px;\n      height: 24px;\n  }\n}\n\n@media (max-width: 500px) { \n  .game-stats { \n      gap: 0.7rem; \n  } \n  .flag-options { \n      gap: 0.5rem; \n  }\n  \n  .modal {\n    padding: 2px; /* Minimal padding on very small screens */\n  }\n  \n  .modal-content {\n    padding: 0.75rem;\n    max-height: min(98vh, 98dvh); /* Use almost full height */\n    border-radius: 12px; /* Smaller radius for small screens */\n  }\n  \n  .modal-footer {\n    padding: 0 0 2rem 0; /* Ensure enough bottom padding for touch */\n  }\n  \n  .high-scores-header, .high-score-entry {\n      font-size: 0.75rem;\n      padding: 0.3rem 0;\n  }\n  \n  /* Further compress for very small screens */\n  .header-name, .entry-name {\n      min-width: 60px;\n  }\n  \n  .header-score, .entry-points,\n  .header-accuracy, .entry-accuracy {\n      min-width: 35px;\n  }\n  \n  .header-date, .entry-date {\n      min-width: 45px;\n  }\n}\n\n/* Additional utility class for better mobile scrolling */\n@media (max-width: 700px) {\n  .modal-content {\n    /* Improve scrolling on iOS */\n    -webkit-overflow-scrolling: touch;\n    /* Ensure content can scroll properly */\n    overflow-y: auto;\n  }\n}\n\n/* High Scores Modal Specific Styles */\n.high-scores-modal {\n  display: flex;\n  justify-content: center; /* Center horizontally */\n  align-items: center; /* Center vertically */\n  padding: 20px; /* Default padding */\n  max-width: 90%; /* Limit width */\n  max-height: 90%; /* Limit height */\n  margin: auto; /* Center the modal */\n  background: white; /* Background color */\n  border-radius: 10px; /* Rounded corners */\n  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Shadow for depth */\n}\n\n.high-scores-modal.mobile {\n  padding: 40px; /* Additional padding for mobile */\n}"],"sourceRoot":""}]);
